.popup {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    visibility: hidden;
    opacity: 0;
    transform: opacity 0.8s linear;
    transition: visibility 0.5s, opacity 0.5s linear;
    padding: 0 19px 0;
} 